<template>
  <div class="projects container">
    <br><br><br>
    <b-loading :is-full-page="isFullPage" :active.sync="isLoading"></b-loading>

          <div v-for="project in projects" v-bind:key="project.id">
            <!-- <h1 style="color:white;">{{$route.params.id}}</h1> -->
              <div v-if="project.id == $route.params.id">
                <h1 class=" has-text-centered has-text-warning is-size-1" style="padding-top:200 !important;" >{{ project.title }}</h1>
                <div v-for="item in project.url" v-bind:key="item">
                  <br><br>
                  <img :src="item" alt="Screenshot-304" border="0" />
                </div>
              </div>


            <!-- <img src="https://i.ibb.co/xJd2v5S/Screenshot-304.png" alt="Screenshot-304" border="0">
