</template>
<script>
// import Card from "@/components/Card.vue";
export default {
  components: {
    // Card
  },
  data: function() {
    return {
      isLoading: false,
      isFullPage: true,
      projects: [],
      baseUrl:
        "https://gist.githubusercontent.com/dannyyol/0aa94087da3f035f1da97d1f1c822757/raw/433e7bf1ed417cafd1a92f283d60714e0b6dfcb3/json"
    };
  },
  created: function() {
    this.open();
    this.getJson();
  },
  methods: {
    getJson: function() {
      fetch(this.baseUrl)
        .then(this.handleErrors)
        .then(response => {
          return response.json();
        })
        .then(myJson => {
          // console.log(myJson);
          this.projects = myJson;
          // this.isLoading = false;
                    console.log(myJson);

        })
        .catch(error => console.log(error));
    },
    handleErrors(response) {
      if (!response.ok) {
        this.$toast.open({ message: "Network Error", type: "is-danger" });
        throw Error(response.statusText);
      }
      return response;
    },
    open() {
      this.isLoading = true;
      setTimeout(() => {
        this.isLoading = false;
      }, 2 * 1000);
    }
  }
};
</script>

